.loader {
  position: absolute;
  top:  calc(50% - 150px);
  left: calc(50% - 150px);
  width: 300px;
  height: 300px;
  animation: pulse 2s infinite;
}
.background {
  width: 516px;
  margin: auto;
}
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

